function calc() {

    //97 98 Calc
    const result = document.querySelector('.calculating__result span');              //   

    let sex, height, weight, age, ratio;

    if(localStorage.getItem('sex')) {                                                //98    sex    
        sex = localStorage.getItem('sex');                                           //98   
    } else {                                                                         //98       LocalStorage
        sex = 'female';                                                              //98    
        localStorage.setItem('sex', 'female');                                       //98    
    }

    if(localStorage.getItem('ratio')) {                                              //98    ratio    
        ratio = localStorage.getItem('ratio');                                       //98   
    } else {                                                                         //98       LocalStorage
        ratio = '1.375';                                                             //98    
        localStorage.setItem('ratio', '1.375');                                      //98    
    }

    function initLocalStorage(selector, activeClass){                                //98 -       localStorage 
        const elements = document.querySelectorAll(selector);                        

        elements.forEach(elem => {                                                   //98     
            elem.classList.remove(activeClass);                                      //98      
            if (elem.getAttribute('id') === localStorage.getItem('sex')){            //98    id ==   LocalStorage
                elem.classList.add(activeClass);                                     //98     
            }
            if(elem.getAttribute('data-ratio') === localStorage.getItem('ratio')) {
                elem.classList.add(activeClass);
            }
        });
    }

    initLocalStorage('#gender div', 'calculating__choose-item_active');
    initLocalStorage('.calculating__choose_big div', 'calculating__choose-item_active');


    function calcTotal() {                                                           // 
        if (!sex || !height || !weight || !age || !ratio) {                          //      
            result.textContent = '____';                                             //    ____
            return;                                                                  //  
        }

        if(sex === 'famale'){                                                        //   
            result.textContent = Math.round((447.6 + (9.2 * weight) + (3.1 * height) - (4.3 * age)) * ratio);
        } else {                                                                     //    )
            result.textContent = Math.round((88.36 + (13.4 * weight) + (4.8 * height) - (5.7 * age)) * ratio);
        }
    }

    calcTotal();

    function getStaticInformation(selector, activeClass) {                            //  
        const elements = document.querySelectorAll(selector);                         //    

        elements.forEach(item => {                                
            item.addEventListener('click', (e) => {
                if(e.target.getAttribute('data-ratio')){                              //    
                    ratio = +e.target.getAttribute('data-ratio');                     //      ratio
                    localStorage.setItem('ratio', +e.target.getAttribute('data-ratio')) //98       
                } else {
                    sex = e.target.getAttribute('id');                                //  id ,   
                    localStorage.setItem('sex', e.target.getAttribute('id'));         //98       
                } 

                elements.forEach( (elem) => {                                         //      
                    elem.classList.remove(activeClass);
                });
                console.log(sex, ratio);
                
                e.target.classList.add(activeClass);                                  //    

                calcTotal();                                                          //    

            });
        });
    }
   
    getStaticInformation('#gender div', 'calculating__choose-item_active');
    getStaticInformation('.calculating__choose_big div', 'calculating__choose-item_active');

    function getDynamicInformation(selector) {                                        //   
        const input = document.querySelector(selector);

        input.addEventListener('input', () => {

            if(input.value.match(/\D/g)){                                             //98    
                input.style.border = '1px solid red';                                 //98   
            } else {
                input.style.border = 'none';
            }

            switch(input.getAttribute('id')) {                                        //       
                case 'height':                                                        //  heiht
                    height = +input.value;                                            //   height      
                    break;                                                            //  
                case 'weight':
                    weight = +input.value;
                    break;
                case 'age': 
                    age = +input.value;
                    break;
            }

            calcTotal();                                                               //    

        });
    }
    getDynamicInformation('#height');
    getDynamicInformation('#weight');
    getDynamicInformation('#age');
}

export default calc;