function getZiro(num) {                                        //-     
    if(num >= 0 && num < 10){                                  //    > 0  < 10 
        return `0${num}`;                                      // 0 +  
    } else {
        return num;
    }
}

function timer(id, deadline  ) {

    //Timer
    //const deadline = new Date()  /* '2022-05-30' */;                //    

    //deadline.setDate(deadline.getDate() + 1);                       //   1 

    function getTimeRemaining(endtime) {
        let days, hours, minutes, seconds;

        const t = Date.parse(endtime) - Date.parse(new Date());     //-      -   
            
            if(t <= 0) {                                            //   <= 0,    0
                days = 0;
                hours = 0;
                minutes = 0;
                seconds = 0;
            } else {
                days = Math.floor(t / (1000 * 60 * 60 * 24)),       //- 
                hours = Math.floor((t / (1000 * 60 * 60)) % 24 ),   //-  
                minutes = Math.floor((t / 1000 / 60) % 60 ),        //- 
                seconds = Math.floor((t / 1000) % 60);              //- 
            }

        return {
            'total': t,
            'days': days,
            'hours': hours,
            'minutes': minutes,
            'seconds': seconds,
        };
    }

    function setClock(selector, endtime) {
        const timer = document.querySelector(selector),             //   
              days = timer.querySelector('#days'),
              hours = timer.querySelector('#hours'),
              minutes = timer.querySelector('#minutes'),
              seconds = timer.querySelector('#seconds'),
              timeInterval = setInterval(updateClock, 1000);        // updateClock  
        updateClock();                                              //            

        function updateClock() {
            const t = getTimeRemaining(endtime);                    //     t

            days.innerHTML = getZiro(t.days);                       //      getTimeRemaining(endtime)     t
            hours.innerHTML = getZiro(t.hours);
            minutes.innerHTML = getZiro(t.minutes);
            seconds.innerHTML = getZiro(t.seconds);

            if(t.total <= 0){                                       //    , 
                clearInterval(timeInterval);                        //   updateClock
            }
        }
    }
    setClock(id, deadline); 

}

export default timer;
export {getZiro}; 