function tabs(tabsSelector, tabsContentSelector, tabsParantSelector, activeClass) {

     //Tabs
    let tabs = document.querySelectorAll(tabsSelector),
        tabsContent = document.querySelectorAll(tabsContentSelector),
        tabsParant = document.querySelector(tabsParantSelector);

    function hideTabContent() {                                    //       
        tabsContent.forEach( (item) => {
            item.classList.remove('fade');                          //  fade 
            item.classList.add('hide');                             //   hide
        });

        tabs.forEach( (item) => {
            item.classList.remove(activeClass);        //  
        });
    }

    function showTabContent(i = 0) {                                //      i=0
        tabsContent[i].classList.add('fade');                       //  fade
        tabsContent[i].classList.remove('hide');                    // arr[0]  hide
        tabs[i].classList.add(activeClass);       // arr[0]  
        
    }

    hideTabContent();
    showTabContent();

    tabsParant.addEventListener('click', (e) => {                   //     
        
        if(e.target && e.target.classList.contains(tabsSelector.slice(1))) {   //        
            tabs.forEach( (item, i) => {                       //   

                if(e.target == item) {                              //         
                    hideTabContent();                            
                    showTabContent(i);                              //       
                }
            });
        }
    });

}

export default tabs;  // 