const postData = async (url, data) => {                                  //      ,   !!!  90
    const res = await fetch(url, {                                       //   json,     res
        method: 'POST',                                            
        headers: {
            'Content-type': 'application/json'                           //     : 
        },
        body: data   
    });
    return await res.json();        //         =>     =>     
};


async function getResours(url) {                                         //      !!!  90
    let res = await fetch(url);

    if(!res.ok){                                                         //    ( 200)
       throw new Error(`Could not fetch ${url}, status: ${res.status}`); //         
    }

    return await res.json();                                             //   json    
};


export {postData};
export {getResours};