import {getZiro} from './timer';

function slider({container, slide, nextArrow, prevArrow, totalCounter, currentCounter, wrapper, field}) {

    // Slider ---    92 /     93
    const slides = document.querySelectorAll(slide ),
          slider = document.querySelector(container),                     //94    
          currentSlide = document.getElementById(currentCounter),
          totalSlide = document.getElementById(totalCounter ),
          next = document.querySelector(nextArrow),
          prev = document.querySelector(prevArrow),
          slideWrapper = document.querySelector(wrapper),
          slideField = document.querySelector(field),
          width = window.getComputedStyle(slideWrapper).width;                   //93    slideWrapper     

    let slideIndex = 1;                                                      //93    
    let offset = 0;                                                          //93        

    //        
    currentSlide.textContent = getZiro(`${slideIndex}`);                     //93          
    totalSlide.textContent = getZiro(slides.length);                         //93      


    slideField.style.width = 100 * slides.length + '%';                      //93         %
    slideField.style.display = 'flex';                                       //93     
    slideField.style.transition = '0.5s all';                                //93  
    slideWrapper.style.overflow = 'hidden';                                  //93       
    

    slides.forEach(slide => {  
                                                     
        slide.style.width = width;                                           //93        width
    });

    slider.style.position = 'relative';                                      //94   

    const indicators = document.createElement('ol');                         //94    
    let dots = [];                                                           //94    

    indicators.classList.add('carousel-indicators');                         //94      css
    slider.append(indicators);

    for(let i = 0; i < slides.length; i++){                                  //94     
        let dot = document.createElement('li');
        dot.classList.add('dot');
        dot.setAttribute('data-slide-to', i + 1);                            //94    

        if(i == 0) {                                                         //94   ,       1
            dot.style.opacity = 1;
        }

        indicators.append(dot);                                              //94   indicators
        dots.push(dot);                                                      //94    
    }

    function stringToNumber (str) {                                           //96        
        return +str.replace(/\D/g, '');
    }

    next.addEventListener('click', () => {
        //if(offset == +width.slice(0, width.length-2) * (slides.length - 1)){  //93  (offset) ==   * -  -1 
        if(offset == stringToNumber(width) * (slides.length - 1)){              //96     ,      ''
            offset = 0;                                                         //93     ,  = 0
        } else {
            offset += stringToNumber(width);                                    //93 slice    'px'   width
        }
        
        slideField.style.transform = `translateX(-${offset}px)`;                //93     offset

        if(slideIndex == slides.length) {                                       //93    () 
            slideIndex = 1;
        }else {
            slideIndex ++;
        }

        currentSlide.textContent = getZiro(`${slideIndex}`);                     //         
        totalSlide.textContent = getZiro(slides.length);                         //     

        dots.forEach(dot => dot.style.opacity = '0.5');                          //94    50%
        dots[slideIndex - 1].style.opacity = 1;                                  //94      1

    });

    prev.addEventListener('click', () => {                                       //93      
        if(offset == 0) {         
            offset = stringToNumber(width) * (slides.length - 1);
        } else {
            offset -= stringToNumber(width);                       
        }
        
        slideField.style.transform = `translateX(-${offset}px)`;

        if(slideIndex == 1) {
            slideIndex = slides.length;
        }else {
            slideIndex --;
        }

        currentSlide.textContent = getZiro(`${slideIndex}`);                     //         
        totalSlide.textContent = getZiro(slides.length);                         //     

        dots.forEach(dot => dot.style.opacity = '0.5');                          //94    50%
        dots[slideIndex - 1].style.opacity = 1;                                  //94      1
    });
    
    dots.forEach( dot => {
        dot.addEventListener('click', (e) => {
            const slideTo = e.target.getAttribute('data-slide-to');              //94       .  

            slideIndex = slideTo;                                                //94     (slideTo)
            offset = stringToNumber(width) * (slideTo - 1);                      //94   slideTo

            slideField.style.transform = `translateX(-${offset}px)`;             //94     

            currentSlide.textContent = getZiro(`${slideIndex}`);                 //         
            totalSlide.textContent = getZiro(slides.length);                     //     

            dots.forEach(dot => dot.style.opacity = '0.5');                      //94    50%
            dots[slideIndex - 1].style.opacity = 1;                              //94      1
        });
    });
}


export default slider;