import {closeModal, showModal} from './modal';
import {postData} from '../services/services';

function forms(formSelector, modalTimerId) {

     //Form ///// Fetch API  /////////////////////////                      // --  Fetch API
     const forms = document.querySelectorAll(formSelector);

     const messages = {                                                     //      
         loading: 'img/form/spinner.svg',
         success: '!     ',
         failure: '-   ...'
     };
 
     forms.forEach( item => {                                               //   
         bindPostData(item);                                                //  postData    
     }); 

     function bindPostData(form){                                           //       
         form.addEventListener('submit', (e) => {
             e.preventDefault();
               
             const statusMessage = document.createElement('img');           //  
             statusMessage.src = messages.loading;                          //  
             statusMessage.style.cssText = `                              
                 display: block;
                 margin: 0 auto;
             `;               
             form.insertAdjacentElement('afterend', statusMessage);         //    form
 
             const formData = new FormData(form);                           //      ,       name
             
             const json = JSON.stringify( Object.fromEntries( formData.entries())  ); //       =>    =>  json 
 
             postData('http://localhost:3000/requests', json)               // postData   
             .then(data => {                                                //   
                 console.log(data);
                 showThanksModal(messages.success);
                 statusMessage.remove();
 
             }).catch(() => {                                               // 
                 showThanksModal(messages.failure);
 
             }).finally(() => {                                             //     
                 form.reset();   
             });
         });
     } 
 
 
     //    
     function showThanksModal(message) {
         const prevModalDiolog = document.querySelector('.modal__dialog');
 
         prevModalDiolog.classList.add('hide');                                   //     
         prevModalDiolog.classList.remove('show');
         showModal('.modal', modalTimerId);
 
         const thanksModal = document.createElement('div');                         
         thanksModal.classList.add('modal__dialog');
         thanksModal.innerHTML = `
             <div class="modal__content">
                 <form action="#">
                     <div data-close class="modal__close">&times;</div>
                     <div class="modal__title">${message}</div>
                 </form>
             </div>
             `;
         document.querySelector('.modal').append(thanksModal);
 
         setTimeout( () => {                                                     
             thanksModal.remove();                                                //  
             prevModalDiolog.classList.remove('hide');                            //  
             prevModalDiolog.classList.add('show');
             closeModal('.modal');                                                        //  
         }, 4000 );
     }
     
}

export default forms;